.navigation {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 100;
}

.navbar {
	position: relative;
	border-radius: 0px;
	margin: 0px;
	padding: 5px;
	background-color: #1a1a1a;
	font-size: 0.855em;
}

.navbar .navbar-nav > li > a {
	color: white;
	height: 100%;
}

.navbar .navbar-nav > li > a:hover {
	color: #8d8c7e;
}

.navbar .navbar-menu-item > li > a {
	color: white;
}

.logo {
	margin-top: -10px;
}

.carousel {
	position: relative;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

.carouselHoverDiv {
	margin-top: -20px;
	position: absolute;
	width: 100%;
	text-align: center;
}

.carouselHoverDiv h1 {
	color: white;
	font-size: 10vw;
	font-weight: bold;
}

.carouselHoverDiv h3 {
	color: white;
	font-size: 5vw;
	margin-bottom: 20px;
}

.carousel b {
	letter-spacing: 1px;
}

.carousel .whiteButton {
	width: 25vw;
	font-size: 15px;
	min-width: 150px;
	height: 50px;
	margin: 0px;
	background-color: rgba(0, 0, 0, 0.2);
	color: white;
	border-width: 5px;
}

.footer {
	color: white;
	background-color: #8d8c7e;
	padding: 10px 0px;
	position: absolute;
	bottom: 0;
	right: 0;
	left: 0;
}

.footer a {
	color: white;
	margin-bottom: 10px;
}

.footer a:hover {
	color: white;
}

.footer .mediaIcons {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.footer .mediaIcon {
	margin: 0px 20px 20px 20px;
}

.footer .gridRow {
	text-align: center;
}

.footer .cell0 {
	padding: 40px 10px 20px 10px;
	
}

.footer .cell1 {
	padding: 20px 10px;
}

.footer .cell1 .button {
	background-color: #ffd602;
	border-color: black;
	width: 200px;
	margin: 0 auto;
}

.footer .cell1 .button:hover {
	background-color: black;
	border-color: #ffd602;
	color: #ffd602;
}

.footer .cell2 {
	padding: 20px;
	color: white;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
}

@media only screen and (min-width: 768px) {
	.footer .cell1 {
		grid-column-start: 3;
	}

	.carousel .whiteButton {
		font-size: 25px;
		height: 70px;
	}

	.carouselHoverDiv h3 {
		margin-bottom: 40px;
	}
}



